<template>
  <svg
    xmlns="http://www.w3.org/2000/svg"
    :width="size.width"
    :height="size.height"
    :viewBox="`0 0 ${size.width} ${size.height}`"
    :aria-labelledby="name"
    role="presentation"
    class="icon-base"
  >
    <transition name="fade">
      <g>
        <component :is="Icons[name]" />
      </g>
    </transition>
  </svg>
</template>

<script setup lang="ts">
import { computed } from "vue";
import { object, string } from "vue-types";
import type { IconName } from "@/shared/ui_kit/icon/Icons";
import Icons, { getIconSize } from "@/shared/ui_kit/icon/Icons";

const props = defineProps({
  name: string<IconName>().def("arrow"),
  size: object<{ width: number; height: number }>(),
});

const size = computed(() => props.size ?? getIconSize(props.name));
</script>

<style scoped lang="scss">
.icon-base {
  fill: currentColor;
}
</style>
