import arrow from "@/shared/ui_kit/icon/functional/IconArrow.vue";
import cancel from "@/shared/ui_kit/icon/functional/IconCancel.vue";
import change from "@/shared/ui_kit/icon/functional/IconChange.vue";
import check from "@/shared/ui_kit/icon/functional/IconCheck.vue";
import listMarker from "@/shared/ui_kit/icon/functional/IconListMarker.vue";
import show from "@/shared/ui_kit/icon/functional/IconShow.vue";
import edit from "@/shared/ui_kit/icon/functional/IconEdit.vue";
import lock from "@/shared/ui_kit/icon/functional/IconLock.vue";
import plus from "@/shared/ui_kit/icon/functional/IconPlus.vue";
import deleteItem from "@/shared/ui_kit/icon/functional/IconDelete.vue";

import authentication from "@/shared/ui_kit/icon/navigation/IconAuthentication.vue";
import cluster from "@/shared/ui_kit/icon/navigation/IconCluster.vue";
import deckhouse from "@/shared/ui_kit/icon/navigation/IconDeckhouse.vue";
import ingressControllers from "@/shared/ui_kit/icon/navigation/IconIngressControllers.vue";
import modules from "@/shared/ui_kit/icon/navigation/IconModules.vue";
import nodesManagement from "@/shared/ui_kit/icon/navigation/IconNodesManagement.vue";
import securityScanner from "@/shared/ui_kit/icon/navigation/IconSecurityScanner.vue";
import updates from "@/shared/ui_kit/icon/navigation/IconUpdates.vue";
import additionalPreferences from "@/shared/ui_kit/icon/navigation/IconAdditionalPreferences.vue";

import settings from "@/shared/ui_kit/icon/content/IconSettings.vue";

import alert from "@/shared/ui_kit/icon/other/IconAlert.vue";
import warning from "@/shared/ui_kit/icon/other/IconWarning.vue";

const functionalIcon = { arrow, cancel, show, edit, lock, change, check, listMarker, plus, deleteItem };

const contentIcon = { settings };

const otherIcon = { alert, warning };

const navigationIcon = {
  authentication,
  cluster,
  deckhouse,
  ingressControllers,
  modules,
  nodesManagement,
  securityScanner,
  updates,
  additionalPreferences,
};

const icons = { ...functionalIcon, ...navigationIcon, ...contentIcon, ...otherIcon } as const;

export const getIconSize = (iconName: IconName) => {
  const iconSize = {
    functionalIcon: { width: 10, height: 10 },
    navigationIcon: { width: 12, height: 12 },
    contentIcon: { width: 20, height: 20 },
    otherIcon: {
      alert: { width: 16, height: 16 },
      warning: { width: 16, height: 16 },
    },
  };
  const iconType = {
    functionalIcon,
    navigationIcon,
    contentIcon,
    otherIcon,
  };

  const [targetIconType] = Object.entries(iconType).find(([, typedIcons]) => Object.keys(typedIcons).some((key) => key === iconName)) as [
    keyof typeof iconType,
    typeof icons
  ];
  if (targetIconType) {
    return targetIconType === "otherIcon" ? iconSize.otherIcon[iconName as keyof typeof otherIcon] : iconSize[targetIconType];
  }

  console.error(`Unknown icon name ${iconName}`);
  return { width: 12, height: 12 };
};

export type IconName = keyof typeof icons;
export default icons;
